@import "../../../styles/theme";

$min-height: 612px;

.section {
  &.--s11 {
    position: relative;
    z-index: 40;
    min-height: $min-height;
    height: $min-height;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 0;

    .---content {
      width: 100vw;
      height: 100%;
      position: absolute;

      .ymap-container {
        width: 100%;
        height: 100%;
      }

      .-s-marker-container {
        width: 331px;
        height: 160px;
        display: flex;
        position: relative;
        .-marker {
          width: 140px;
          height: 140px;
          background-color: white;
          border-radius: 70px;
          position: relative;
          display: flex;
          justify-content: center;
          align-items: center;
          box-shadow: 0px 2px 10px rgba(171, 171, 171, 0.5);
          z-index: 115;
          &:before {
            content: "";
            position: absolute;
            bottom: -20px;
            width: 0;
            height: 0;
            border-left: 30px solid transparent;
            border-right: 30px solid transparent;
            border-top: 50px solid white;
          }
        }
      }
      .-marker-content {
        z-index: 114;
        position: absolute;
        width: 331px;
        height: 140px;
        background: #FFFFFF;
        border-radius: 0 15px 15px 0;
        left: 70px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding-left: 90px;
        white-space: pre;
        box-sizing: border-box;
        .-m-title {
          font-family: $font-Nunito-Bold;
          font-size: 24px;
          line-height: 33px;
          color: #000000;
          margin-top: 10px;
        }
        .-m-address, .-m-time {
          font-family: $font-Nunito-Regular;
          font-size: 14px;
          line-height: 19px;
          color: #000000;
          text-align: left;
        }
        .-m-time {
          margin-top: 9px;
        }
      }
    }

  }
}


@media only screen and (max-width: 1216px) {

  .section {
    &.--s11 {
      margin: 0 !important;
      height: 375px;
      min-height: 375px;
      padding: {
        right: 0;
        left: 0;
      }

      .---content {
        width: 100vw;
        height: 100%;
        position: absolute;

        .-s-marker-container {
          width: 300px;
          height: 146px;
          display: flex;
          position: relative;

          .-marker {
            width: 80px;
            height: 80px;
            border-radius: 40px;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: 0px 2px 10px rgba(171, 171, 171, 0.5);
            z-index: 115;

            &:before {
              content: "";
              position: absolute;
              bottom: -15px;
              width: 0;
              height: 0;
              border-left: 15px solid transparent;
              border-right: 15px solid transparent;
              border-top: 30px solid white;
            }
          }
        }

        .-marker-content {
          z-index: 114;
          position: absolute;
          width: 260px;
          height: 136px;
          background: #FFFFFF;
          border-radius: 15px 15px 15px 0;
          left: 40px;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: flex-start;
          padding-left: 50px;
          white-space: pre;
          box-sizing: border-box;
          top: -57px;
          .-m-title {
            font-size: 18px;
            line-height: 25px;
          }

          .-m-address, .-m-time {
            font-size: 13px;
            line-height: 18px;
          }

          .-m-time {
            margin-top: 9px;
          }
        }


      }

    }
  }
}
